'use client';

import Link from 'next/link';
import { useState } from 'react';

const Navbar = () => {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };

  return (
    <header className="sticky top-0 z-50 bg-background-light/95 backdrop-blur-md border-b border-[#f4f3f1] dark:border-white/5 transition-all duration-300">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between h-20">
          <div className="flex items-center gap-3">
            <div className="flex items-center justify-center size-10 rounded-full bg-primary/10 text-primary">
              <span className="material-symbols-outlined text-2xl">soup_kitchen</span>
            </div>
            <span className="text-xl font-bold tracking-tight text-text-main">Rasa Nusantara</span>
          </div>
          <nav className={`hidden md:flex items-center gap-8 ${isMobileMenuOpen ? 'block' : ''}`}>
            <Link className="text-sm font-medium text-text-main hover:text-primary transition-colors" href="#menu">Menu</Link>
            <Link className="text-sm font-medium text-text-main hover:text-primary transition-colors" href="#story">Cerita</Link>
            <Link className="text-sm font-medium text-text-main hover:text-primary transition-colors" href="#location">Lokasi</Link>
            <Link className="text-sm font-medium text-text-main hover:text-primary transition-colors" href="#reviews">Ulasan</Link>
          </nav>
          <div className="hidden md:flex">
            <button className="flex items-center justify-center h-10 px-6 rounded-full bg-primary text-white text-sm font-bold shadow-soft hover:bg-primary-hover hover:scale-105 transition-all duration-300">
              Pesan Online
            </button>
          </div>
          <div className="flex md:hidden">
            <button 
              onClick={toggleMobileMenu}
              className="text-text-main p-2"
              aria-label="Toggle mobile menu"
            >
              <span className="material-symbols-outlined">{isMobileMenuOpen ? 'close' : 'menu'}</span>
            </button>
          </div>
        </div>
        
        {/* Mobile menu */}
        {isMobileMenuOpen && (
          <div className="md:hidden py-4 border-t border-[#f4f3f1]">
            <nav className="flex flex-col gap-4">
              <Link 
                className="text-sm font-medium text-text-main hover:text-primary transition-colors py-2" 
                href="#menu"
                onClick={() => setIsMobileMenuOpen(false)}
              >
                Menu
              </Link>
              <Link 
                className="text-sm font-medium text-text-main hover:text-primary transition-colors py-2" 
                href="#story"
                onClick={() => setIsMobileMenuOpen(false)}
              >
                Cerita
              </Link>
              <Link 
                className="text-sm font-medium text-text-main hover:text-primary transition-colors py-2" 
                href="#location"
                onClick={() => setIsMobileMenuOpen(false)}
              >
                Lokasi
              </Link>
              <Link 
                className="text-sm font-medium text-text-main hover:text-primary transition-colors py-2" 
                href="#reviews"
                onClick={() => setIsMobileMenuOpen(false)}
              >
                Ulasan
              </Link>
              <button className="flex items-center justify-center h-10 px-6 rounded-full bg-primary text-white text-sm font-bold shadow-soft hover:bg-primary-hover hover:scale-105 transition-all duration-300 w-full">
                Pesan Online
              </button>
            </nav>
          </div>
        )}
      </div>
    </header>
  );
};

export default Navbar;